/* eslint no-undef: 0 */
/* eslint arrow-parens: 0 */
import React from 'react';
import { enquireScreen } from 'enquire-js';

import Nav0 from './Nav0';
import Banner0 from './Banner0';
import Feature3 from './Feature3';
import Feature0 from './Feature0';
import Footer1 from './Footer1';
import Content5 from './Content5';
import {
  Nav00DataSource,
  Banner01DataSource,
  Feature30DataSource,
  Feature01DataSource,
  Feature31DataSource,
  Feature03DataSource,
  Feature04DataSource,
  Feature09DataSource,
  Feature010DataSource,
  Feature07DataSource,
  Footer10DataSource,
  Content50DataSource,
  Content51DataSource
} from './data.source';
import './less/antMotionStyle.less';

let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

const { location = {} } = typeof window !== 'undefined' ? window : {};

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
      show: !location.port, // 如果不是 dva 2.0 请删除
    };
  }

  componentDidMount() {
    // 适配手机屏幕;
    enquireScreen((b) => {
      this.setState({ isMobile: !!b });
    });
    // dva 2.0 样式在组件渲染之后动态加载，导致滚动组件不生效；线上不影响；
    /* 如果不是 dva 2.0 请删除 start */
    if (location.port) {
      // 样式 build 时间在 200-300ms 之间;
      setTimeout(() => {
        this.setState({
          show: true,
        });
      }, 500);
    }
    /* 如果不是 dva 2.0 请删除 end */
  }

  render() {
    const children = [
      <Nav0
        id="Nav0_0"
        key="Nav0_0"
        dataSource={Nav00DataSource}
        isMobile={this.state.isMobile}
      />,
      <Banner0
        id="Banner0_1"
        key="Banner0_1"
        dataSource={Banner01DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature3
        id="Feature3_0"
        key="Feature3_0"
        dataSource={Feature30DataSource}
        isMobile={this.state.isMobile}
        style={{"background":"#f7f9fc"}}
      />,
      <Content5
      id="Content5_0"
      key="Content5_0"
      dataSource={Content50DataSource}
      isMobile={this.state.isMobile}
      />,
      <Feature0
        id="Feature0_1"
        key="Feature0_1"
        dataSource={Feature01DataSource}
        isMobile={this.state.isMobile}
        style={{"background":"#f7f9fc"}}
      />,
      <Feature3
        id="Feature3_1"
        key="Feature3_1"
        dataSource={Feature31DataSource}
        isMobile={this.state.isMobile}
      />,
      // <Feature0
      //   id="Feature0_3"
      //   key="Feature0_3"
      //   dataSource={Feature03DataSource}
      //   isMobile={this.state.isMobile}
      //   style={{"background":"#f7f9fc"}}
      // />,
      <Feature0
        id="Feature0_4"
        key="Feature0_4"
        dataSource={Feature04DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature0
        id="Feature0_9"
        key="Feature0_9"
        dataSource={Feature09DataSource}
        isMobile={this.state.isMobile}
        style={{"background":"#f7f9fc"}}
      />,
      <Feature0
        id="Feature0_10"
        key="Feature0_10"
        dataSource={Feature010DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature0
        id="Feature0_7"
        key="Feature0_7"
        dataSource={Feature07DataSource}
        isMobile={this.state.isMobile}
        style={{"background":"#f7f9fc"}}
      />,
      <Content5
      id="Content5_0"
      key="Content5_0"
      dataSource={Content51DataSource}
      isMobile={this.state.isMobile}
      />,
      <Footer1
        id="Footer1_0"
        key="Footer1_0"
        dataSource={Footer10DataSource}
        isMobile={this.state.isMobile}
      />,
    ];
    return (
      <div
        className="templates-wrapper"
        ref={(d) => {
          this.dom = d;
        }}
      >
        {/* 如果不是 dva 2.0 替换成 {children} start */}
        {this.state.show && children}
        {/* 如果不是 dva 2.0 替换成 {children} end */}
      </div>
    );
  }
}
